* {
  box-sizing: border-box;
}

html {
  font-family: f-light, sans-serif;
}

body {
  margin: 0;
  background-color: $clr14;
}

p {
  margin: 0;
}

img {
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.screen {
  position: fixed;
  inset: 0;
  pointer-events: none;
  &.open {
    pointer-events: all;
  }
}
